<template>
 <div>
    <div class="bg-light mx-lg-10" style="background-color:#2e6599 !important; color:#fff;">
      <div class="container content-space-2">
        <div class="row justify-content-center">
          <div class="col-sm-12 col-md-4 mailclick">
            <div class="text-center">
              <h2 @click="openmail()" id="contactmail" class="display-5">Click to Start Trial Session</h2>
            </div>
          </div>
        </div>
      </div>
    </div>

      
    <div id="contactus" class="bg-light mx-lg-10 mailform">
      <div class="container content-space-2 content-space-lg-3">
      <form ref="form" @submit.prevent="sendEmail">
        <div class="row">
          <div class="col-sm-6 mailcol">
            <h4 class="mb-3">서비스 문의 및 체험판 신청</h4>
            <label class="form-label">Name</label>
             <input id="namearea" class="form-control" name="user_name" placeholder="성함을 입력해 주세요">
            <label class="form-label">Email</label>
             <input id="mailarea" class="form-control" type="email" name="user_email" placeholder="E-mail을 입력해 주세요">
            <label class="form-label">Message</label>
              <textarea id="textarea" class="form-control" name="message" placeholder="문의 내용을 입력해 주세요."></textarea>
            <input class="btn btn-primary" type="submit" value="보내기">
          </div>
        </div>
      </form>
      
      </div>
      
  </div>  

  </div>


</template>

<script>
import emailjs from '@emailjs/browser';

export default {
     name: 'MailForm',
     methods: {
    openmail() {
      if(/* eslint-disable */$("#contactus").css("display") === "none"){
         /* eslint-disable */$("#contactus").css("display","block");
        document.body.scrollIntoView(false);
        $('#contactmail').html("Go to Home");
       }
      else { /* eslint-disable */$("#contactus").css("display","none");
        document.body.scrollIntoView(true);
        $('#contactmail').html("Click to start trial Session");
      }
    },

    sendEmail() {
      emailjs.sendForm('service_bvnamyk', 'template_tenwpgd', this.$refs.form, 'udZxz8W6B070C3u3a')
        .then((result) => {
            console.log('SUCCESS!', result.text);     
            alert('Your mail is sent!');
            document.getElementById('namearea').value="";
            document.getElementById('emailarea').value="";
            document.getElementById('textarea').value="";

        }, (error) => {
            console.log('FAILED...', error.text);
            alert('Can not sent the mail');
            document.getElementById('namearea').value="";
            document.getElementById('emailarea').value="";
            document.getElementById('textarea').value="";
        });
    }
  }
}
</script>

<style scoped>

#contactmail { cursor:pointer;}
.mailclick {
  width:100%;
  padding-top:20px;
  padding-bottom:20px;
 
}

.mailclick h2 { font-size:40px !important;}

#contactus {display:none;}
.mailform {
  background:#ededed;
  margin:0px; 
  color:rgb(0, 0, 0);
  padding: 100px;  
}

.mailcol {    margin: 0px auto;}
.form-control { margin-bottom:30px;}

#textarea {height:150px; }

</style>