import { createWebHistory, createRouter } from "vue-router";
import Home from "./components/Home.vue"
import LoginPage from "./components/DashBoard/LoginPage"
import DashBoard from "./components/DashBoard/DashBoard"
import SepFS from "./components/Routes/SepFS"
import ConsFS from "./components/Routes/ConsFS"
import DSDChk from "./components/Routes/DSDChk"
import DashMain from "./components/Routes/DashMain"

const routes = [
    {
        path: "/",
            name: "Home",
        component: Home,
    },

    {
        path: "/login",
        name: "Login",
        component: LoginPage,
    },
    {
        path: "/dash",
        name: "DashBoard",
        component: DashBoard,
        children:[
            {
                path: "dashmain",
                name: "DashMain",
                component: DashMain,
            },
            {
                path: "sepfs",
                name: "SepFS",
                component: SepFS,
            },
            {
                path: "consfs",
                name: "ConsFS",
                component: ConsFS,
            },
            {
                path: "dsdchk",
                name: "DSDChk",
                component: DSDChk,
            },

        ]
    }
];

const router = createRouter({
    history: createWebHistory(),
    routes,
});

export default router;

